	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Mandrake On The Notebook","date":"08 Jul 2002","slug":"/2002-07-08-mandrake-on-the-notebook/","tags":"Y2002"},"html":"<p>After struggling for some hours trying <a href=\"http://www.papascott.de/2002/06/23/1781.php#001781\">to get WiFi to work with a FreeBSD notebook</a> and my Linux router, I gave up. The notebook would purr lovingly when I put the other card in my FreeBSD workstation, but stubbornly refused to talk to Linux. Since I just want the thing to work, I gave up and put Mandrake 8.2 on the notebook. It's what we use at work, so I can keep updated, and it took just 2 tries to get online. I'm still not completely wireless, since the battery is still shot, but I can get that repaired or replaced. And there's a power outlet on the patio. And the weather is supposed to get better.</p>"},"next":{"fields":{"title":"Apple.de?","date":"08 Jul 2002","slug":"/2002-07-08-applede/"}},"previous":{"fields":{"title":"Seconds To Decide","date":"08 Jul 2002","slug":"/2002-07-08-seconds-to-decide/"}}},{"node":{"fields":{"title":"Apple.de?","date":"08 Jul 2002","slug":"/2002-07-08-applede/","tags":"Y2002"},"html":"<p>I know that Apple has never done all that well in Germany, but did they really intend to <a href=\"http://www.papascott.de/images/fotos/applede1.php\" onclick=\"window.open('http://www.papascott.de/images/fotos/applede1.php', 'popup', 'width=482,height=297,scrollbars=no,resizable=no,toolbar=no,directories=no,location=no,menubar=no,status=no,left=0,top=0'); return false\">cancel</a> their <a href=\"http://www.apple.de\">.de domain</a>? It must be a <a href=\"http://www.heise.de/newsticker/data/hps-08.07.02-000/\">mistake</a>. (Via <a href=\"http://www.gnurps.de/\">gnurps</a>)</p>"},"next":{"fields":{"title":"Allovee","date":"07 Jul 2002","slug":"/2002-07-07-allovee/"}},"previous":{"fields":{"title":"Mandrake On The Notebook","date":"08 Jul 2002","slug":"/2002-07-08-mandrake-on-the-notebook/"}}},{"node":{"fields":{"title":"Allovee","date":"07 Jul 2002","slug":"/2002-07-07-allovee/","tags":"Y2002"},"html":"<p>Just to document this for posperity, Christopher's word for television is 'Allovee'. That's the sound the new Samsung TV makes when we turn it on.</p>"},"next":{"fields":{"title":"TrackBack For The Rest Of The World","date":"06 Jul 2002","slug":"/2002-07-06-trackback-for-the-rest-of-the-world/"}},"previous":{"fields":{"title":"Apple.de?","date":"08 Jul 2002","slug":"/2002-07-08-applede/"}}}],"pathPrefix":"","first":false,"last":false,"index":875,"pageCount":1101}}

